.casestudy {
  width: 100%;
  max-width: 768px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: auto;
}

.text-container {
  width: 80%;
}

.casestudy-info {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
}

.tools,
.timeframe {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  text-align: center;
  width: 45%;
}

.casestudy-title {
  margin: 20px 0 10px 0;
}

.casestudy-img {
  width: 100%;
}

.imageGroup {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

#mp03,
#mp04,
#mp05 {
  margin: 10px;
  width: 45%;
}

#ksAnimatic {
  margin: 10px;
  width: 45%;
}

#ksDesign {
  margin: 10px;
  width: 90%;
}

#ks07 {
  width: 60%;
}



@media screen and (max-width: 500px) {
  #mp03,
  #mp04,
  #mp05 {
    width: 70%;
  }
}