    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  .column .thumbnail:hover .hoverOverThumbnail,
  .column .thumbnail:focus .hoverOverThumbnail {
    -webkit-transform: scale(1);
            transform: scale(1);
